.parent-proyect {
    width: 100%;
    background: transparent;
    color: white;
    overflow: hidden;
    display: flex;
    gap: 20px;
}
.body-proyect {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.6;
}
  
.header-proyect {
    color: white;
    font-size: x-large;
    padding: 1rem 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.header-proyect h1 {
    font-size: 5rem;
    text-shadow: 
    0 0 5px #a6039e, /* Brillo interno */
    0 0 10px #ff69b4, /* Segunda capa */
    0 0 20px #ff69b4; /* Luz difusa */
    animation: glow 1.5s infinite alternate;
  }
.header-proyect h3{
    border: 2px solid #9C27B0;
    background-color: #2c1b47;
    padding: 7px 15px;
    border-radius: 10px;
    text-shadow: 0 0 5px #a6039e, 0 0 10px #ff69b4, 0 0 20px #ff69b4;
    box-shadow: 0 0 20px #ff00ff;
    cursor: pointer;
}
.child-left-proyect {
    flex: 1;
    text-align: left;
    padding: 2rem;
  }
.child-left-proyect h2{
    font-size: xxx-large;
  }
.description-proyect p{
    color: #d73cbe;
    padding: 5px;
  }
.list-habilidades{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    list-style: none; 
    padding: 0;
    margin: 20px 0;
}
.list-habilidades img {
    width: 80px;
    height: auto;
  }
.list-habilidades li {
    transition: transform 0.3s ease, box-shadow 0.3s ease; 
}

.list-habilidades li:hover {
    transform: scale(1.1);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); 
}
.child-rigth-proyect {
    padding: 2rem;
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
.slider{
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  
.slider-container {
    display: flex;
    width: 100%;
    height: 800px; 
    gap: 15px; 
    overflow: hidden; 
    padding: 20px;
    justify-content: space-evenly;
}
.image-slider{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    height: 500px;
    background-color: #2c1b4730;
    border-radius: 25px;
    box-shadow: 0 0 20px #ff00ff;
    transition: all 0.3s ease;
    overflow: hidden;
}
.image-proyecto {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    inset: 0;
}
  
.image-proyecto img {
    width: 80%;
    height: auto;
    border-radius: 10px;
}

.parent-contactos {
    padding: 2rem;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.social-container {
    display: flex;
    width: fit-content;
    gap: 100px;
    padding: 15px 25px;
}
  
.social-icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #ff00ff;
    position: relative;
    transition: transform 0.3s ease, background-color 0.3s ease;
}
  
.social-icon::after {
    content: "";
    width: 30px;
    height: 30px;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
}
  
.instagram::after {
    background-image: url('../../assets/icons8-instagram.svg');
}
  
.linkedin::after {
    background-image: url('https://img.icons8.com/?size=100&id=gwguOe4urWnW&format=png&color=D102F9');
}
  
.github::after {
    background-image: url('https://img.icons8.com/?size=100&id=3tC9EQumUAuq&format=png&color=D102F9');
}
  
.facebook::after {
    background-image: url('https://img.icons8.com/?size=100&id=CRoXl9uzpuXc&format=png&color=D102F9');
}
  
.social-icon:hover {
    transform: scale(1.1);
    background-color: rgba(255, 0, 255, 0.2);
}
  
.footer {
    background: rgba(0, 0, 0, 0.25);
    color: white;
    padding: 1rem;
    font-size: 0.9rem;
}
 @keyframes glow {
    from {
      text-shadow: 
        0 0 5px #ff69b4,
        0 0 10px #ff69b4,
        0 0 20px #ff69b4;
    }
    to {
      text-shadow: 
        0 0 10px #ff69b4,
        0 0 20px #ff69b4,
        0 0 30px #ff69b4;
    }
}